import React, { useState, useEffect } from "react";
import styled, {keyframes, css} from 'styled-components';

//device break point
const size = {
    mobileS: 320,
    mobileM: 375,
    mobileL: 425,
    tablet: 768,
    laptop: 1024,
    laptopL: 1440,
    desktop: 2560
}

const device = {
    mobileS: `(min-width: ${size.mobileS}px)`,
    mobileM: `(min-width: ${size.mobileM}px)`,
    mobileL: `(min-width: ${size.mobileL}px)`,
    tablet: `(min-width: ${size.tablet}px)`,
    laptop: `(min-width: ${size.laptop}px)`,
    laptopL: `(min-width: ${size.laptopL}px)`,
    desktop: `(min-width: ${size.desktop}px)`,
    desktopL: `(min-width: ${size.desktop}px)`
};  


const HomeBody = styled.div`
    width: 100%;
    height: 100vh;
    background-color: #111010;
    position: relative;
    display: flex;
    flex-flow: row wrap;
    overflow: hidden;

    @media ${device.mobileS} {
        background-color: green;
    }
    @media ${device.mobileM} {
        background-color: red;
    }
    @media ${device.mobileL} {
        background-color: #111010;
    }
    @media ${device.tablet} {
        background-color: #111010;
    }
    @media ${device.laptop} {
        background-color: #111010;
    }
    @media ${device.laptopL} {
        background-color: #111010;
    }
    @media ${device.desktop} {
        background-color: #111010;
    }
    @media ${device.desktopL} {
        background-color: #111010;
    }
`;
const textPathKeyframes = keyframes`
    0%{
    }
    100% {
        fill: orange;
    }
`;
const HomeSVG = styled.svg`
    z-index: 5;
    width: ${props => props.width ? css`${props.width}rem`: ""};
    height: ${props => props.height ? css`${props.height}rem` : ""};
    margin: 10rem auto;
    color: white;;
    font-size: 7.1rem;
    //border: 3px solid purple;
    @media ${device.tablet} {
        font-size: 5.55rem;
    }
    @media ${device.laptop} {
        font-size: 7.18rem;
    }
    textPath {
 
    }
    text {
        fill: #111010;
        font-family: 'Secular One', sans-serif;
        animation: ${textPathKeyframes} 2s 3s ease forwards;
    }
`; 
const HomeSVGPath = styled.path`
    fill: transparent;
    stroke: transparent;
`; 
const HomeH1 = styled.div`
    z-index: 5;
    position: absolute;
    width: 100%;
    height: 10rem;
    font-size: 10rem;
    text-align: center;
    color: white;
    margin-top: 25rem;
    font-family: 'Secular One', sans-serif;
    transition: all 1s ease;
    opacity: ${props => props.opacity == "true" ? 1 : 0};
    @media ${device.tablet} {
        font-size: 7rem;
    }
    @media ${device.laptop} {
        font-size: 10rem;
    }
`;
